const MyPhotos = () => {
    const photos = [
      "/albums/01.jpg",
      "/albums/02.jpg",
      "/albums/03.jpg",
      "/albums/04.jpg",
      "/albums/05.jpg",
    ];
  
    return (
      <div className="bg-white shadow rounded p-4">
        <div className="flex justify-between mb-4">
          <h5 className="text-lg font-semibold">Photos</h5>
          <a href="#" className="text-sm text-blue-500">See all</a>
        </div>
        <div className="grid grid-cols-3 gap-2">
          {photos.map((photo, index) => (
            <img key={index} src={photo} alt={`Photo ${index}`} className="rounded w-full h-auto" />
          ))}
        </div>
      </div>
    );
  };
  
  export default MyPhotos;
  